.footer {
  background: #343532;
  padding: 60px 0;

  & .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.footer_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
}

.footer_developer {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 24px;

  & svg {
    margin-left: 4px;
  }

  &:hover {
    color: #3F8DEE;

    & svg {
      fill: #3F8DEE;
    }
  }
}

.footer_wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.footer_copyright {
  font-size: 12px;
  line-height: 120%;
  position: relative;
  color: white;
  display: inline;
  text-decoration: underline;

  &:hover {
    cursor: pointer;
    color:  #3F8DEE;
  }
}

.footer-information {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  line-height: 120%;
  padding-top: 10px;
  display: none;
  color: white;

  & p {
    margin-bottom: 15px;
  }

  a {
    color: white;

    &:hover {
      color:  #3F8DEE;
    }
  }

  &.open {
    display: block;
  }
}
