.offers {
  flex-grow: 1;
  background: #F0F2F5;
  padding-top: 60px;
  padding-bottom: 60px;

  & .center {
    display: flex;
    flex-direction: column;
  }
}

.offets_title {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 15px;
  color: #100f0f;
  text-align: center;
}

.offers_subtitle {
  font-size: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
}

.offers_list {
  flex-grow: 1;
  padding-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
}

.offers_item {
  background: #FFFFFF;
  border: 0.5px solid #A3A3A3;
  box-sizing: border-box;
  border-radius: 4px;
  display: none;
  flex-direction: column;
  padding: 19px 23px;
  position: relative;

  &--best {
    padding-top: 35px;
  }

  &.hidden {
    display: none;
  }

  &.active {
    display: flex;
  }
}

.offers_best {
  background: #30B935;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  padding-right: 12px;
  padding-left: 12px;
  min-height: 25px;
  box-sizing: border-box;
  position: absolute;
  top: -1px;
  left: -0.5px;
  right: -0.5px;
  display: flex;
  align-items: center;

  & svg {
    margin-right: 6px;
  }
}

.offers_img {
  margin-bottom: 25px;
}

.offers_item--best .offers_img {
  margin-bottom: 20px;
}

.offers_wrapper {
  display: flex;
  flex-direction: row;
}

.offers_conditions {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: #000000;
  display: flex;
  flex-direction: column;
  white-space: nowrap;

  &:first-of-type {
    margin-right: 15px;
    flex-basis: 107px;
  }
}

.offers_number {
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
}

.offers_unit {
  font-weight: bold;
}

.offers_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #B5B5B5;
}

.offers_button {
  background: #E21313;
  border-radius: 4px;
  border: 1px solid #E21313;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  margin-top: 30px;
  padding: 16px 0;
  text-align: center;
  text-decoration: none;


  &:hover {
    background: #3F8DEE;
    border-color: #3F8DEE;
    cursor: pointer;
  }
}

.offers_item--best .offers_button {
  margin-top: 19px;
}

.offers_link {
  background: #E21313;
  border-radius: 4px;
  border: 1px solid #E21313;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  margin-top: 40px;
  padding: 16px 16px;
  text-align: center;
  text-decoration: none;
  display: block;
  align-self: center;


  &:hover {
    background: #3F8DEE;
    border-color: #3F8DEE;
    cursor: pointer;
  }
}

@media(max-width: 990px) {
  .offers_list {
    grid-template-columns: 1fr 1fr;
  }
}

@media(max-width: 767px) {
  .offers_list {
    padding-top: 15px;
  }

  .offers_list {
    grid-template-columns: 1fr;
    grid-gap: 15px;
  }

  .offers_item {
    padding: 15px;
    min-width: auto;

    &--best {
      padding-top: 23px;
    }
  }

  .offers_best {
    font-size: 11px;
    line-height: 13px;
    min-height: 20px;

    & svg {
      width: 11px;
      height: 15px;
      margin-right: 5px;
    }
  }

  .offers_img {
    margin-bottom: 15px;
    width: 100px;
    height: 44px;
  }
  
  .offers_item--best .offers_img {
    margin-bottom: 9px;
  }

  .offers_conditions {
    font-size: 15px;
  
    &:first-of-type {
      margin-right: 15px;
      flex-basis: 92px;
    }
  }
  
  .offers_number {
    font-size: 20px;
  }

  .offers_button {
    margin-top: 11px;
  }
  
  .offers_item--best .offers_button {
    margin-top: 9px;
  }
}