body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.center {
  max-width: 1110px;
  width: calc(100% - 30px);
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}

@media(max-width: 990px) {
  .center {
    max-width: 690px;
  }
}

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0;
}

p {
  margin: 0;
}

.main {
  flex-grow: 1;
  padding-top: 90px;
  display: flex;
  flex-direction: column;
}

@media(max-width: 767px) {
  .main {
    padding-top: 60px;
  }
}