.banner {
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.banner_img {
  margin-bottom: 20px;
}

.banner_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 25px;
}

.banner_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  color: #000000;
  max-width: 730px;

  & span {
    font-weight: bold;;
  }
}

@media(max-width: 767px) {
  .banner {
    padding: 40px 0;
  }

  .banner_title {
    font-size: 21px;
    line-height: 120%;
    margin-bottom: 15px;
  }

  .banner_subtitle {
    font-size: 14px;
    line-height: 150%;
  }
}