.header {
  background: #fff;
  box-shadow: 0 3px 8px rgba(0,0,0,.08);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 9999;

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    min-height: 90px;
  }
}

.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:hover .line {
    stroke: #2b4fa9;
  }
}

.hamRotate.active {
  transform: rotate(45deg);
}

.line {
  fill:none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke:#000;
  stroke-width:5.5;
  stroke-linecap:round;
}
.ham8 .top {
  stroke-dasharray: 40 160;
}
.ham8 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.ham8 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
.ham8.active .top {
  stroke-dashoffset: -64px;
}
.ham8.active .middle {
  transform: rotate(90deg);
}
.ham8.active .bottom {
  stroke-dashoffset: -64px;
}

.menu {
  background: white;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: scale(0);
  position: fixed;
  margin-top: 0;
  padding-left: 0;
  padding-top: calc(30px + 90px);
  padding-bottom: 30px;
  box-sizing: border-box;

  &.active {
    transform: scale(1);
  }
}

.menu_wrapper {
  transform: scale(0);
  transition: all 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
}

.menu.active .menu_wrapper {
  transform: scale(1);
  transition: all 0.5s ease-in-out ;
}

.menu_link {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  margin-bottom: 10px;

  &:hover {
    color: #2b4fa9
  }
}

@media(max-width: 767px) {
  .header .center {
    min-height: 60px;
  }

  .menu {
    padding-top: calc(30px + 60px);
    padding-bottom: 30px;
  }

  .header_logo {
    width: 88px;
  }
}